.PhotoTreeMap{
  width: 0;
  height: 0;
  opacity: 0;
  -webkit-transition: width 0.5s, height 0.5s, opacity 1s;
  -o-transition: width 0.5s, height 0.5s, opacity 1s;
  transition: width 0.5s, height 0.5s, opacity 1s;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 10vh;
}
.PhotoTreeMap.started{
  opacity: 1;
  width: 90vw;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.PhotoTreeMap>.col1>#breadcrumbs{
  height: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction:row;
      flex-direction:row;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
}
.PhotoTreeMap>.col1{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction:column;
      flex-direction:column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  width: 70vw;
  height: 80vh;
}
.PhotoTreeMap>.col2{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction:column;
      flex-direction:column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  width: 100%;
  height: 80vh;
}
.PhotoTreeMap .paper{
  background: #FAFAFA;
  -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.24), 0px 0px 2px rgba(0, 0, 0, 0.12);
          box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.24), 0px 0px 2px rgba(0, 0, 0, 0.12);
  border-radius: 2px;
  padding: 10px 15px;
}
.PhotoTreeMap .paper.paper__no-padding{
  padding: 0;
}
.PhotoTreeMap .paper>.paper--title{
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 15px;
  color: #333333;
}
.PhotoTreeMap ::-webkit-scrollbar-thumb {
  border-radius: 2px;
  border: 2px solid #ccc;
  background: #ccc;
  /*-webkit-box-shadow: inset 0 0 2px rgba(0,0,0,.5);*/
}
.PhotoTreeMap ::-webkit-scrollbar {
  width: 8px;
}


